.menu{
    text-align: center;
    padding: 15px;
    font-size: 18px;
    position: fixed;

    .hidden{
        width: 40px;

        hr { 
            display: block; 
            border: 0; 
            border-top: 5px solid rgb(0, 0, 0);
         }

        .show{

            display: none;

            
    a{
        background-color: rgb(3, 238, 160);
        text-decoration: none;
        color: black;
        padding: 15px;
        border: 4px solid whitesmoke;
    }

    a:hover{
        background-color: whitesmoke;
        color: black;
    }
        }
    }

    .hidden:hover{

        .show{
            display: grid;
            grid-template-columns: 1fr;
            width: 300px;
            height: 100px;

        }
    }


}



@media (max-width: 650px)
{
    .menu{
        width: 100vw;
        font-size:auto;

        a{
            background-color: white;
            border: 0.5px solid black;
            padding-right: 10px;
            margin-bottom: 5px;
        }
    }
}